body {
  margin: 0;
  font-family: "Handjet", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(239, 246, 255);

  background: radial-gradient(ellipse at bottom, #dfebf8 0%, #a3b1f9 100%);
  overflow: hidden;
  width: 100vw;
  height: 100vh;
  font-weight: 300;
  font-variation-settings: "ELGR" 1, "ELSH" 16;
}

