.header {
  display: flex;
  margin-left: 2%;
  margin-right: 2%;
  height: 10vh;
  justify-content: space-between;
  align-items: center;
}

.title-links {
  display: flex;
  justify-content: center;
  align-items: "center";
}

.title {
  text-decoration: none;
  color: #fbd6ee;
  font-size: 100%;
  /* font-size: 10vh; */
  font-weight: 300;
  font-family: "Silkscreen";
  white-space: nowrap;
  vertical-align: bottom;
}

.links-container {
  display: flex;
  list-style-type: none;
  padding: 0;
  margin: 0;
  margin-left: 2%;
  align-items: center;
  justify-content: center;
  margin-bottom: -1.5%;
}

.icon {
  vertical-align: bottom;
  width: 30px;
  height: 30px;
  color: white;
}

.menu-box {
  position: fixed;
  /* top: 2%; */
  right: 2%;
  z-index: 2;
}

.menu-container {
  outline: solid 2px #fbd6ee;
  padding: 4px 10px 4px 10px;
  background-color: white;
  box-shadow: 2px 3px 0px #fbdff1;
  background-color: 2s 500ms;
  font-size: 20px;
  font-weight: 600;
  border-radius: 10px;
  text-align: center;
  width: auto;
}

#menu-container-sub {
  outline: solid 2px #fbd6ee;
  padding: 4px 10px;
  background-color: white;
  box-shadow: 2px 3px 0px #fbdff1;
  font-size: 20px;
  font-weight: 600;
  display: inline-block;
  border-radius: 10px;
  max-height: 0;
  overflow: hidden;
  transform-origin: top;
  transform: scaleY(0);
  opacity: 0;
  transition: transform 0.5s ease-in-out, max-height 0.5s ease-in-out,
    opacity 0.5s ease-in-out, padding 0.5s ease-in-out,
    outline-color 0.5s ease-in-out;
  position: absolute;
  right: 0;
  text-align: center;
  width: auto;
}

#menu-container-sub.closed {
  max-height: 0;
  transform: scaleY(0);
  opacity: 0;
  padding: 0;
  outline-color: transparent;
}

#menu-container-sub.open {
  max-height: 300px;
  transform: scaleY(1);
  opacity: 1;
  /* padding: 4px 10px;
  outline-color: #6f5c4c; */
}

#menu-container-sub li {
  padding: 5px;
  list-style-type: none;
}

.clover-background {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 60vw;
  height: 60vh;
  background-image: url("./assets/images/Clover.png");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  transform: translate(-50%, -50%);
  z-index: -2;
  animation: tilt-shaking 2s infinite cubic-bezier(0.25, 0.25, 0.25, 0.25);
}

@keyframes tilt-shaking {
  0% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  20% {
    transform: translate(-50%, -50%) rotate(3deg);
  }
  50% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  80% {
    transform: translate(-50%, -50%) rotate(-3deg);
  }
  100% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
}

